import { useEffect, useState } from "react";
import { useFeedbackQuestionCreate } from "../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import FormInput from "../../../Ui/Input/FormInput";
import FormTextarea from "../../../Ui/Input/FormTextarea";
const FeedbackAddQuestion = ({
  onClose,
  ifid,
  onRefetch,
  questionCount,
  question,
  isAuthority,
  onEditAction,
  optionCount,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    question_sno: questionCount ? `${questionCount + 1}` : "",
    question_title: "",
    options: [
      {
        option_sno: "1",
        option: "",
      },
      {
        option_sno: "2",
        option: "",
      },
    ],
  });
  const [errorField, setErrorField] = useState("");
  const [feedbackQuestionCreate] = useFeedbackQuestionCreate();

  useEffect(() => {
    if (questionCount >= 0) {
      setState((prev) => ({
        ...prev,
        question_sno: `${questionCount + 1}`,
      }));
    }
  }, [questionCount]);

  useEffect(() => {
    if (question) {
      let opt = [];

      for (let ot of question?.options) {
        opt.push({
          option_sno: ot?.option_sno,
          option: ot?.option,
        });
      }
      setState((prev) => ({
        ...prev,
        question_sno: question?.question_sno
          ? question?.question_sno
          : `${questionCount + 1}`,
        question_title: question?.question_title,
        options: opt,
      }));
    }
  }, [question]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const addQuestionOption = () => {
    if (optionCount > state?.options?.length) {
      let questions = state;
      if (questions?.options?.length < 5) {
        questions?.options?.push(
          addFeedbackQuestionOption(questions?.options?.length + 1)
        );
        setState((prev) => ({ ...questions }));
      }
    }
  };
  const removeQuestionOption = (optIndex) => {
    let questions = state;
    questions.options = questions.options?.filter(
      (ot) => ot?.option_sno !== `${optIndex + 1}`
    );
    let count = 1;
    for (let o of questions.options) {
      o.option_sno = `${count}`;
      ++count;
    }
    setState((prev) => ({ ...questions }));
  };

  const onQuestionOptionChange = (e, optIndex) => {
    const inputarr = { ...state };
    inputarr.options[optIndex].option = e.target.value;
    setState((prev) => ({ ...inputarr }));
  };

  const onAddQuestion = () => {
    let obj = {
      question_title: state.question_title,
    };
    for (let rt of state.options) {
      obj[rt.option_sno] = rt.option;
    }

    let validation = requiredField(obj, []);
    if (existingRequiredField(validation)) {
      if (isAuthority) {
        onEditAction(state);
      } else {
        if (ifid) {
          setDisabled((pre) => !pre);
          feedbackQuestionCreate({
            ifid: ifid,
            questionCreate: { questions: state },
          })
            .then(() => {
              onRefetch();
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch(() => {
              setDisabled((pre) => !pre);
            });
        }
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("add_question")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <FormTextarea
            label={`${t("question")} :  ${state?.question_sno}`}
            placeholder={t("question_placeholder")}
            name={"question_title"}
            value={state?.question_title}
            type={"text"}
            onChange={onChange}
            errorShow={errorField["question_title"]}
          />
          {state?.options?.map((opt, optIndex) => (
            <FormInput
              key={`option${optIndex}`}
              label={`${t("option")} : ${opt?.option_sno}`}
              placeholder={t("option_placeholder")}
              name={`option${optIndex}`}
              value={opt?.option}
              type={"text"}
              onChange={(e) => onQuestionOptionChange(e, optIndex)}
              errorShow={errorField[opt[optIndex]]}
              customFormInputLabel={{
                display: "flex",
                alignItmes: "center",
                justifyContent: "space-between",
              }}
            >
              {optIndex < 2 ? null : (
                <CreateButton
                  label={t("remove")}
                  customStyle={{
                    margin: "0 0 0 0",
                    padding: "0.1rem 0.5rem",
                    width: "fit-content",
                    fontSize: "12px",
                    fontWeight: "400",
                  }}
                  onAction={() => removeQuestionOption(optIndex)}
                />
              )}
            </FormInput>
          ))}
          {optionCount <= state?.options?.length ? null : (
            <CreateButton
              label={t("add_option")}
              customStyle={{
                margin: "0",
                marginTop: "1rem",
                padding: "0.2rem 1rem",
                width: "fit-content",
              }}
              onAction={addQuestionOption}
            />
          )}
          <CreateButton
            label={t(isAuthority ? "update" : "feedback_add_question")}
            onAction={onAddQuestion}
          />
        </UserMemberContentWrapper>
        {disabled && <QvipleLoader />}
      </div>
    </Modal>
  );
};

export default FeedbackAddQuestion;

const addFeedbackQuestionOption = (args) => {
  return {
    option_sno: `${args}`,
    option: "",
  };
};
